<template>
  <div class="field-analysis-container">
    <div class="section-header">
      <h2>高精度测绘与数字孪生</h2>
      <p>融合航测影像、多光谱数据与地面传感器，构建农田全要素数字模型。</p>
    </div>

    <div class="analysis-content">
      <div class="upload-section">
        <div 
          class="upload-box" 
          :class="{ 'is-dragover': isDragOver, 'has-file': fileInfo }"
          @click="$refs.fileInput.click()"
          @dragover.prevent="isDragOver = true"
          @dragleave.prevent="isDragOver = false"
          @drop.prevent="handleDrop"
        >
          <input type="file" ref="fileInput" accept=".tif,.tiff,.kml,.kmz" @change="handleFileChange" style="display: none;" />
          
          <div v-if="!fileInfo" class="placeholder-content">
            <i class="fa fa-layer-group"></i>
            <p>拖拽 TIF/KMZ 文件到此处，或点击上传</p>
            <div class="tags">
              <span class="tag">正射影像</span> <span class="tag">DSM高程</span> <span class="tag">多光谱NDVI</span>
            </div>
          </div>
          
          <div v-else class="file-preview">
            <div class="file-icon"><i class="fa fa-file-contract"></i></div>
            <div class="file-info">
              <h4>{{ fileInfo.name }}</h4>
              <p>{{ fileInfo.size }} MB | 类型: 自动识别 (多光谱 TIF)</p>
            </div>
            <button class="remove-btn" @click.stop="resetUpload"><i class="fa fa-trash"></i></button>
          </div>
        </div>
        
        <button class="process-btn" @click="processModel" :disabled="!fileInfo || isProcessing">
          <i class="fa" :class="isProcessing ? 'fa-spinner fa-spin' : 'fa-cube'"></i>
          {{ isProcessing ? '数字模型构建中...' : '生成数字孪生模型' }}
        </button>
      </div>

      <div v-if="modelReady" class="workbench">
        <div class="workbench-layout">
          <aside class="layer-control">
            <h3><i class="fa fa-database"></i> 数据图层</h3>
            <div class="layer-list">
              <label class="layer-item">
                <input type="checkbox" v-model="visibleLayers.boundary"> 
                地块边界 (RTK)
              </label>
              <label class="layer-item">
                <input type="checkbox" v-model="visibleLayers.dsm"> 
                三维地形 (DSM)
              </label>
              <label class="layer-item">
                <input type="checkbox" v-model="visibleLayers.ndvi"> 
                作物长势 (NDVI)
              </label>
            </div>

            <div class="sensor-box">
              <h3><i class="fa fa-wifi"></i> 地面物联感知</h3>
              <div class="sensor-grid">
                <div class="sensor-item">
                  <span class="label">土壤湿度</span>
                  <span class="value">24%</span>
                </div>
                <div class="sensor-item">
                  <span class="label">空气温度</span>
                  <span class="value">28℃</span>
                </div>
                <div class="sensor-item warning">
                  <span class="label">风速</span>
                  <span class="value">5.2m/s</span>
                  <i class="fa fa-exclamation-circle" title="风速偏大"></i>
                </div>
              </div>
            </div>
          </aside>

          <div class="map-viewport">
             <div id="digitalTwinMap" class="map-canvas"></div>
             <div class="map-overlay-info">
               <span>面积: 485.2 亩</span>
               <span>平均 NDVI: 0.72</span>
             </div>
          </div>

          <aside class="analysis-panel">
             <div class="panel-header">长势分析与 VRT 处方</div>
             
             <div class="growth-chart">
               <div class="bar-group">
                 <div class="bar-label">优 (30%)</div>
                 <div class="bar-track"><div class="bar-fill green" style="width: 30%"></div></div>
               </div>
               <div class="bar-group">
                 <div class="bar-label">中 (50%)</div>
                 <div class="bar-track"><div class="bar-fill yellow" style="width: 50%"></div></div>
               </div>
               <div class="bar-group">
                 <div class="bar-label">差 (20%)</div>
                 <div class="bar-track"><div class="bar-fill red" style="width: 20%"></div></div>
               </div>
             </div>

             <div class="prescription-card">
               <h4><i class="fa fa-flask"></i> 变量追肥建议</h4>
               <div class="rule-item">
                 <span class="rule-name">弱势区 (NDVI &lt; 0.4)</span>
                 <span class="rule-action">增量 20%</span>
               </div>
               <div class="rule-item">
                 <span class="rule-name">强势区 (NDVI &gt; 0.7)</span>
                 <span class="rule-action">减量 10%</span>
               </div>
               <button class="generate-btn" @click="generateTask">
                 生成 VRT 航线任务
               </button>
             </div>
          </aside>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AIFieldBoundaryAnalysis',
  data() {
    return {
      isDragOver: false,
      isProcessing: false,
      modelReady: false,
      fileInfo: null,
      mapInstance: null,
      visibleLayers: {
        boundary: true,
        dsm: false,
        ndvi: true
      },
      // 模拟地块
      boundaryPath: [
        [116.397, 39.909], [116.402, 39.911], [116.405, 39.907], [116.401, 39.905]
      ]
    };
  },
  watch: {
    visibleLayers: {
      handler() { this.updateMapLayers(); },
      deep: true
    }
  },
  methods: {
    // --- 上传逻辑 ---
    handleDrop(e) {
      this.isDragOver = false;
      const file = e.dataTransfer.files[0];
      if (file) this.processFileDetails(file);
    },
    handleFileChange(e) {
      const file = e.target.files[0];
      if (file) this.processFileDetails(file);
    },
    processFileDetails(file) {
      this.fileInfo = {
        name: file.name,
        size: (file.size / 1024 / 1024).toFixed(2),
        file: file
      };
      this.modelReady = false; // 重置结果
    },
    resetUpload() {
      this.fileInfo = null;
      this.modelReady = false;
    },

    // --- 模型处理 ---
    processModel() {
      this.isProcessing = true;
      // 模拟处理时间
      setTimeout(() => {
        this.isProcessing = false;
        this.modelReady = true;
        this.$nextTick(() => {
          this.initMap();
        });
      }, 2000);
    },

    // --- 地图可视化 ---
    initMap() {
      if (!window.AMap) return;
      this.mapInstance = new window.AMap.Map('digitalTwinMap', {
        center: [116.401, 39.908],
        zoom: 15,
        viewMode: '3D',
        pitch: 50 // 倾斜视角展示 DSM 效果
      });
      this.updateMapLayers();
    },

    updateMapLayers() {
      if (!this.mapInstance) return;
      this.mapInstance.clearMap();

      if (this.visibleLayers.boundary) {
        new window.AMap.Polygon({
          path: this.boundaryPath,
          strokeColor: "#FF33FF",
          strokeWeight: 4,
          fillColor: 'transparent',
          map: this.mapInstance
        });
      }

      // 模拟 NDVI 热力图图层
      if (this.visibleLayers.ndvi && window.AMap.Heatmap) {
         // 这里简化处理，实际应加载图片图层
         const heatmap = new window.AMap.Heatmap(this.mapInstance, {
             radius: 30, opacity: [0, 0.6],
             gradient: { 0.4: 'red', 0.6: 'yellow', 0.9: 'green' }
         });
         const points = this.generateMockPoints();
         heatmap.setDataSet({ data: points, max: 100 });
      }
    },
    
    generateMockPoints() {
      return [
        {lng: 116.401, lat: 39.908, count: 90}, // 绿
        {lng: 116.398, lat: 39.909, count: 40}, // 红
        {lng: 116.403, lat: 39.906, count: 60}  // 黄
      ];
    },

    generateTask() {
      alert('VRT 航线任务已生成！\n\n已根据 NDVI 差异将地块划分为 3 个作业区，预计节省药液 15%。');
    }
  }
};
</script>

